import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Badge } from '../components/ui/badge';
import { Button } from '../components/ui/button';
import { 
  BuildingOfficeIcon, 
  HeartIcon, 
  EnvelopeIcon,
  StarIcon,
  SparklesIcon,
  CheckIcon
} from '@heroicons/react/24/outline';
import { apiService } from '../services/api';
import { sponsorBenefits, sponsorNotes } from '../constants/sponsorBenefits';

const SponsorsPage = () => {
  const [sponsors, setSponsors] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchSponsors();
  }, []);

  const fetchSponsors = async () => {
    try {
      setLoading(true);
      const response = await apiService.getSponsors();
      setSponsors(response.data.data || []);
    } catch (error) {
      console.error('Error fetching sponsors:', error);
      setError('Khng th ti danh sch nh ti tr');
    } finally {
      setLoading(false);
    }
  };

  const getTierConfig = (tier) => {
    const configs = {
      Diamond: {
        name: 'Kim cng',
        borderColor: 'border-cyan-400',
        bgColor: 'border-cyan-400/20',
        priority: 1
      },
      Platinum: {
        name: 'Bch kim',
        borderColor: 'border-gray-400',
        bgColor: 'border-gray-400/20',
        priority: 2
      },
      Gold: {
        name: 'Vng',
        borderColor: 'border-yellow-400',
        bgColor: 'border-yellow-400/20',
        priority: 3
      },
      Silver: {
        name: 'Bc',
        borderColor: 'border-gray-400',
        bgColor: 'border-gray-400/20',
        priority: 4
      },
      Bronze: {
        name: 'ng',
        borderColor: 'border-amber-600',
        bgColor: 'border-amber-600/20',
        priority: 5
      },
      Supporter: {
        name: 'H tr',
        borderColor: 'border-green-400',
        bgColor: 'border-green-400/20',
        priority: 6
      }
    };
    return configs[tier] || configs.Bronze;
  };

  // Sort sponsors by tier priority
  const sortedSponsors = sponsors.sort((a, b) => {
    const tierA = getTierConfig(a.currentTier);
    const tierB = getTierConfig(b.currentTier);
    return tierA.priority - tierB.priority;
  });

  const getParticipationYears = (sponsor) => {
    if (!sponsor.sponsorshipHistory || sponsor.sponsorshipHistory.length === 0) return '';
    const years = sponsor.sponsorshipHistory.map(h => h.year).sort((a, b) => b - a);
    if (years.length === 1) return years[0];
    return `${Math.min(...years)} - ${Math.max(...years)}`;
  };

  if (loading) {
    return (
      <div className="container py-8">
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="container py-8">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4">Nh ti tr</h1>
          <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
            Chng ti trn trng s ng hnh v h tr ca cc nh ti tr trong vic t chc 
            cuc thi Web Design Contest, to c hi hc tp v pht trin cho sinh vin, hc sinh.
          </p>
        </div>

        {error && (
          <div className="mb-8 p-4 bg-destructive/10 text-destructive rounded-lg text-center">
            {error}
          </div>
        )}

        {sponsors.length === 0 && !loading ? (
          <div className="text-center py-12">
            <BuildingOfficeIcon className="h-16 w-16 mx-auto text-muted-foreground mb-4" />
            <h3 className="text-lg font-medium mb-2">Cha c nh ti tr no</h3>
            <p className="text-muted-foreground">Thng tin nh ti tr s c cp nht sm nht</p>
          </div>
        ) : (
          /* Sponsors Grid */
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6 mb-16">
            {sortedSponsors.map((sponsor) => {
              const tierConfig = getTierConfig(sponsor.currentTier);
              const participationYears = getParticipationYears(sponsor);

              return (
                <Card 
                  key={sponsor.id} 
                  className={`transition-all duration-300 hover:shadow-lg border-2 ${tierConfig.borderColor} ${tierConfig.bgColor}`}
                >
                  <CardContent className="p-6">
                    {/* Logo */}
                    <div className="flex justify-center mb-4">
                      {sponsor.logo ? (
                        <img
                          src={sponsor.logo}
                          alt={`${sponsor.name} logo`}
                          className="h-16 w-auto object-contain"
                        />
                      ) : (
                        <div className="h-16 w-16 bg-muted rounded-lg flex items-center justify-center">
                          <BuildingOfficeIcon className="h-8 w-8 text-muted-foreground" />
                        </div>
                      )}
                    </div>

                    {/* Sponsor Info */}
                    <div className="text-center">
                      <h3 className="font-semibold text-lg mb-2">{sponsor.name}</h3>
                      
                      {/* Tier Badge */}
                      <Badge 
                        variant="outline" 
                        className={`mb-3 ${tierConfig.borderColor}`}
                      >
                        {tierConfig.name}
                      </Badge>

                      {/* Participation Years */}
                      {participationYears && (
                        <p className="text-sm text-muted-foreground mb-3">
                          Tham gia: {participationYears}
                        </p>
                      )}

                      {/* Description */}
                      {sponsor.description && (
                        <p className="text-sm text-muted-foreground line-clamp-3">
                          {sponsor.description}
                        </p>
                      )}

                      {/* Website Link */}
                      {sponsor.website && (
                        <div className="mt-3">
                          <Button variant="outline" size="sm" asChild>
                            <a 
                              href={sponsor.website} 
                              target="_blank" 
                              rel="noopener noreferrer"
                              className="text-xs"
                            >
                              Trang web
                            </a>
                          </Button>
                        </div>
                      )}
                    </div>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        )}

        {/* Sponsor Benefits Tables */}
        <div className="mb-16">
          <h2 className="text-3xl font-bold text-center mb-8">Quyn li nh ti tr</h2>
          
          {/* Basic Benefits Table */}
          <Card className="mb-8">
            <CardHeader className="">
              <CardTitle className="text-center text-xl">Quyn li nh ti tr c bn</CardTitle>
            </CardHeader>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b bg-muted/50">
                      <th className="text-left p-4 font-semibold">Quyn li nh ti tr</th>
                      <th className="text-center p-4 font-semibold">Vng</th>
                      <th className="text-center p-4 font-semibold">Bc</th>
                      <th className="text-center p-4 font-semibold">ng</th>
                    </tr>
                  </thead>
                  <tbody>
                    {sponsorBenefits.basic.map((benefit, index) => (
                      <tr key={index} className={`border-b hover:bg-muted/50 ${index % 2 === 0 ? 'bg-background' : 'bg-muted/20'}`}>
                        <td className="p-4 font-medium">{benefit.category}</td>
                        <td className="p-4 text-center">
                          {typeof benefit.gold === 'boolean' ? (
                            benefit.gold ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.gold}
                        </td>
                        <td className="p-4 text-center">
                          {typeof benefit.silver === 'boolean' ? (
                            benefit.silver ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.silver}
                        </td>
                        <td className="p-4 text-center">
                          {typeof benefit.bronze === 'boolean' ? (
                            benefit.bronze ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.bronze}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </CardContent>
          </Card>

          {/* Special Benefits Table */}
          <Card className="mb-8">
            <CardHeader className="">
              <CardTitle className="text-center text-xl">Quyn li nh ti tr c bit</CardTitle>
            </CardHeader>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b bg-muted/50">
                      <th className="text-left p-4 font-semibold">Quyn li nh ti tr</th>
                      <th className="text-center p-4 font-semibold">Vng</th>
                      <th className="text-center p-4 font-semibold">Bc</th>
                      <th className="text-center p-4 font-semibold">ng</th>
                    </tr>
                  </thead>
                  <tbody>
                    {sponsorBenefits.special.map((benefit, index) => (
                      <tr key={index} className={`border-b hover:bg-muted/50 ${index % 2 === 0 ? 'bg-background' : 'bg-muted/20'}`}>
                        <td className="p-4 font-medium">{benefit.category}</td>
                        <td className="p-4 text-center">
                          {typeof benefit.gold === 'boolean' ? (
                            benefit.gold ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.gold}
                        </td>
                        <td className="p-4 text-center">
                          {typeof benefit.silver === 'boolean' ? (
                            benefit.silver ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.silver}
                        </td>
                        <td className="p-4 text-center">
                          {typeof benefit.bronze === 'boolean' ? (
                            benefit.bronze ? <CheckIcon className="h-5 w-5 text-green-600 mx-auto" /> : ''
                          ) : benefit.bronze}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </CardContent>
          </Card>

          {/* Additional Notes */}
          <Card className="border-amber-200 bg-amber-50 dark:bg-amber-950/20">
            <CardHeader>
              <CardTitle className="text-amber-800 dark:text-amber-200">Lu  quan trng</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="text-sm text-amber-700 dark:text-amber-300 space-y-2">
                {sponsorNotes.map((note, index) => (
                  <li key={index}> <strong>{note.split('.')[0]}.</strong> {note.split('.').slice(1).join('.')}</li>
                ))}
              </ul>
            </CardContent>
          </Card>
        </div>

        {/* Call to Action */}
        <Card className="mt-16 bg-gradient-to-r from-primary/10 to-secondary/10">
          <CardContent className="text-center py-12">
            <HeartIcon className="h-16 w-16 mx-auto text-primary mb-6" />
            <h2 className="text-2xl font-bold mb-4">Tr thnh nh ti tr</h2>
            <p className="text-lg text-muted-foreground mb-6 max-w-2xl mx-auto">
              Gia nhp cng chng ti trong vic h tr v pht trin ti nng tr trong lnh vc 
              thit k web. C hi qung b thng hiu v kt ni vi sinh vin ti nng.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button size="lg" asChild>
                <a href="mailto:it.mpclub@ou.edu.vn">
                  <EnvelopeIcon className="h-5 w-5 mr-2" />
                  Lin h ti tr
                </a>
              </Button>
              <Button variant="outline" size="lg">
                <BuildingOfficeIcon className="h-5 w-5 mr-2" />
                Tm hiu thm
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Statistics */}
        <div className="grid gap-6 md:grid-cols-4 mt-16">
          <Card className="text-center">
            <CardContent className="pt-6">
              <div className="text-3xl font-bold text-primary mb-2">
                {sponsors.length}
              </div>
              <p className="text-muted-foreground">Nh ti tr</p>
            </CardContent>
          </Card>
          
          <Card className="text-center">
            <CardContent className="pt-6">
              <div className="text-3xl font-bold text-primary mb-2">
                {sponsors.filter(s => s.currentTier === 'Diamond' || s.currentTier === 'Platinum' || s.currentTier === 'Gold').length}
              </div>
              <p className="text-muted-foreground">i tc chnh</p>
            </CardContent>
          </Card>
          
          <Card className="text-center">
            <CardContent className="pt-6">
              <div className="text-3xl font-bold text-primary mb-2">500+</div>
              <p className="text-muted-foreground">Th sinh tham gia</p>
            </CardContent>
          </Card>
          
          <Card className="text-center">
            <CardContent className="pt-6">
              <div className="text-3xl font-bold text-primary mb-2">50+</div>
              <p className="text-muted-foreground">D n xut sc</p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default SponsorsPage;