/* eslint-disable no-unused-vars */
import { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Badge } from '../components/ui/badge';
import { Input } from '../components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '../components/ui/select';
import { CalendarIcon, MagnifyingGlassIcon, PhotoIcon } from '@heroicons/react/24/outline';
import { apiService } from '../services/api';

const ExhibitionsPage = () => {
  const [exhibitions, setExhibitions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [pagination, setPagination] = useState({
    currentPage: 1,
    totalPages: 1,
    hasNext: false
  });
  const [filters, setFilters] = useState({
    search: '',
    status: 'all'
  });

  useEffect(() => {
    fetchExhibitions(1, true);
  }, [filters]);

  const fetchExhibitions = async (page = 1, reset = false) => {
    try {
      if (reset) {
        setLoading(true);
      } else {
        setLoadingMore(true);
      }

      const params = {
        page,
        perPage: 12,
        ...(filters.search && { search: filters.search }),
        ...(filters.status !== 'all' && { status: filters.status })
      };

      const response = await apiService.getExhibitions(params);
      const data = response.data;

      if (reset) {
        setExhibitions(data.exhibitions || []);
      } else {
        setExhibitions(prev => [...prev, ...(data.exhibitions || [])]);
      }

      setPagination({
        currentPage: data.pagination?.currentPage || 1,
        totalPages: data.pagination?.pageCount || 1,
        hasNext: data.pagination?.hasNext || false
      });

    } catch (error) {
      console.error('Error fetching exhibitions:', error);
      
      // Fallback data
      if (reset) {
        setExhibitions([
          {
            _id: '1',
            title: "Web Design Contest 2023 - Trin lm tc phm",
            description: "Trin lm cc tc phm xut sc t cuc thi thit k web 2023 vi s tham gia ca hn 100 th sinh t cc trng i hc trn ton quc.",
            image: "/img/contest-bg.jpg",
            contestCode: "WDC2023",
            startDate: "2023-11-01T00:00:00.000Z",
            endDate: "2023-11-30T23:59:59.999Z",
            status: "completed",
            totalWorks: 25
          },
          {
            _id: '2',
            title: "Showcase Frontend Technologies",
            description: "Trng by cc cng ngh frontend hin i qua cc d n thc t c pht trin bi sinh vin.",
            image: "/img/contest-bg.jpg",
            contestCode: "WDC2022",
            startDate: "2022-12-01T00:00:00.000Z",
            endDate: "2022-12-31T23:59:59.999Z",
            status: "completed",
            totalWorks: 18
          },
          {
            _id: '3',
            title: "UI/UX Design Excellence",
            description: "Nhng thit k UI/UX n tng t sinh vin khoa CNTT vi nhiu  tng sng to.",
            image: "/img/contest-bg.jpg",
            contestCode: "WDC2021",
            startDate: "2021-10-15T00:00:00.000Z",
            endDate: "2021-11-15T23:59:59.999Z",
            status: "completed",
            totalWorks: 30
          }
        ]);
      }
    } finally {
      setLoading(false);
      setLoadingMore(false);
    }
  };

  const loadMore = () => {
    if (pagination.hasNext && !loadingMore) {
      fetchExhibitions(pagination.currentPage + 1, false);
    }
  };

  const handleFilterChange = (key, value) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const getStatusBadge = (status) => {
    switch (status) {
      case 'active':
        return <Badge variant="default">ang din ra</Badge>;
      case 'upcoming':
        return <Badge variant="secondary">Sp din ra</Badge>;
      case 'completed':
        return <Badge variant="outline"> kt thc</Badge>;
      default:
        return <Badge variant="outline">{status}</Badge>;
    }
  };

  return (
    <div className="container py-8 space-y-8">
      {/* Header */}
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold">Trin lm cc tc phm</h1>
        <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
          Khm ph nhng tc phm xut sc t cc cuc thi thit k web. 
          Mi trin lm l mt hnh trnh khm ph s sng to v ti nng ca cc th sinh.
        </p>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4 max-w-2xl mx-auto">
        <div className="relative flex-1">
          <MagnifyingGlassIcon className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Tm kim trin lm..."
            value={filters.search}
            onChange={(e) => handleFilterChange('search', e.target.value)}
            className="pl-10"
          />
        </div>
        <Select value={filters.status} onValueChange={(value) => handleFilterChange('status', value)}>
          <SelectTrigger className="w-full sm:w-48">
            <SelectValue placeholder="Trng thi" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">Tt c</SelectItem>
            <SelectItem value="active">ang din ra</SelectItem>
            <SelectItem value="upcoming">Sp din ra</SelectItem>
            <SelectItem value="completed"> kt thc</SelectItem>
          </SelectContent>
        </Select>
      </div>

      {/* Content */}
      {loading ? (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {[...Array(6)].map((_, index) => (
            <Card key={index} className="overflow-hidden">
              <div className="aspect-video bg-muted animate-pulse"></div>
              <CardHeader>
                <div className="h-4 bg-muted rounded animate-pulse mb-2"></div>
                <div className="h-3 bg-muted rounded animate-pulse w-3/4"></div>
              </CardHeader>
              <CardContent>
                <div className="h-3 bg-muted rounded animate-pulse mb-2"></div>
                <div className="h-3 bg-muted rounded animate-pulse w-1/2"></div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : exhibitions.length > 0 ? (
        <>
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {exhibitions.map((exhibition) => (
              <Card key={exhibition._id} className="group overflow-hidden hover:shadow-lg transition-all duration-300">
                <div className="relative aspect-video overflow-hidden">
                  <img
                    src={exhibition.image || "/img/contest-bg.jpg"}
                    alt={exhibition.title}
                    className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                    loading="lazy"
                  />
                  <div className="absolute top-2 right-2">
                    {getStatusBadge(exhibition.status)}
                  </div>
                </div>
                
                <CardHeader>
                  <CardTitle className="text-lg line-clamp-2">{exhibition.title}</CardTitle>
                  <CardDescription className="line-clamp-3">{exhibition.description}</CardDescription>
                </CardHeader>
                
                <CardContent className="space-y-3">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <CalendarIcon className="h-4 w-4" />
                    <span>{formatDate(exhibition.startDate)} - {formatDate(exhibition.endDate)}</span>
                  </div>
                  
                  <div className="flex justify-between items-center">
                    <div className="text-sm">
                      <span className="text-muted-foreground">Cuc thi:</span> 
                      <span className="font-medium ml-1">{exhibition.contestCode}</span>
                    </div>
                    {exhibition.totalWorks && (
                      <div className="text-sm text-muted-foreground">
                        {exhibition.totalWorks} tc phm
                      </div>
                    )}
                  </div>
                  
                  <Button asChild className="w-full">
                    <Link to={`/exhibitions/${exhibition._id}`}>
                      Xem trin lm
                    </Link>
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>

          {/* Load More Button */}
          {pagination.hasNext && (
            <div className="text-center">
              <Button
                variant="outline"
                size="lg"
                onClick={loadMore}
                disabled={loadingMore}
                className="min-w-32"
              >
                {loadingMore ? (
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-current"></div>
                ) : (
                  'Ti thm'
                )}
              </Button>
            </div>
          )}
        </>
      ) : (
        <div className="text-center py-12">
          <PhotoIcon className="h-16 w-16 mx-auto text-muted-foreground mb-4" />
          <h3 className="text-lg font-medium mb-2">Khng tm thy trin lm no</h3>
          <p className="text-muted-foreground">
            {filters.search || filters.status !== 'all' 
              ? 'Th thay i b lc  tm kim trin lm khc'
              : 'Cc trin lm s c cp nht sm nht'
            }
          </p>
        </div>
      )}
    </div>
  );
};

export default ExhibitionsPage;