/**
 * Gii m JWT token
 * @param {string} token - JWT token cn gii m
 * @returns {Object|null} Phn payload  gii m hoc null nu li
 */
export function decodeJwt(token) {
  try {
    if (!token) return null;
    
    const base64Url = token.split('.')[1];
    if (!base64Url) return null;
    
    const jsonPayload = atob(base64Url);
    return JSON.parse(jsonPayload);
  } catch (error) {
    console.error('Error decoding JWT:', error);
    return null;
  }
}

/**
 * Kim tra xem token c ht hn cha
 * @param {string} token - JWT token cn kim tra
 * @returns {boolean} True nu token  ht hn
 */
export function isTokenExpired(token) {
  try {
    const decoded = decodeJwt(token);
    if (!decoded || !decoded.exp) return true;
    
    const currentTime = Date.now() / 1000;
    return decoded.exp < currentTime;
  } catch (error) {
    return true;
  }
}

/**
 * Ly thi gian ht hn t token
 * @param {string} token - JWT token
 * @returns {Date|null} Thi gian ht hn hoc null nu li
 */
export function getTokenExpiration(token) {
  try {
    const decoded = decodeJwt(token);
    if (!decoded || !decoded.exp) return null;
    
    return new Date(decoded.exp * 1000);
  } catch {
    return null;
  }
}