/* eslint-disable no-unused-vars */
import { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../components/ui/card';
import { Badge } from '../components/ui/badge';
import { Separator } from '../components/ui/separator';
import { Alert, AlertDescription } from '../components/ui/alert';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs';
import { Avatar, AvatarFallback, AvatarImage } from '../components/ui/avatar';
import { 
  UserIcon, 
  ShieldCheckIcon, 
  KeyIcon, 
  CubeIcon,
  ArrowPathIcon,
  EyeIcon,
  EyeSlashIcon,
  ComputerDesktopIcon,
  CalendarIcon,
  MapPinIcon,
  DevicePhoneMobileIcon,
  BuildingOfficeIcon,
  EnvelopeIcon,
  GlobeAltIcon
} from '@heroicons/react/24/outline';

import CodeBlock from '../components/common/CodeBlock';
import { useCopyToClipboard } from '../hooks/useCopyToClipboard';
import { useNotification } from '../hooks/useNotification';
import { decodeJwt } from '../utils/jwtUtils';

const ProfilePage = () => {
  const { currentUser, userInfo, refreshUserInfo } = useAuth();
  const [firebaseToken, setFirebaseToken] = useState('');
  const [backendToken, setBackendToken] = useState('');
  const [refreshing, setRefreshing] = useState(false);
  const [showFirebaseToken, setShowFirebaseToken] = useState(false);
  const [showBackendToken, setShowBackendToken] = useState(false);
  const { copyToClipboard } = useCopyToClipboard();
  const { notification, showSuccess, showError } = useNotification();

  useEffect(() => {
    getTokens();
  }, [currentUser]);

  const getTokens = async () => {
    if (currentUser) {
      try {
        const idToken = await currentUser.getIdToken(true);
        setFirebaseToken(idToken);
      } catch {
        console.error('Error getting Firebase token');
      }
    }
    
    const storedBackendToken = localStorage.getItem('backendToken');
    if (storedBackendToken) {
      setBackendToken(storedBackendToken);
    }
  };

  const handleCopyToClipboard = (text, label = 'Text') => {
    const success = copyToClipboard(text);
    if (success) {
      showSuccess(` sao chp ${label} vo clipboard!`);
    } else {
      showError(`Khng th sao chp ${label}. Vui lng th li.`);
    }
  };

  const handleRefresh = async () => {
    setRefreshing(true);
    try {
      await refreshUserInfo();
      await getTokens();
      showSuccess(' cp nht thng tin thnh cng!');
    } catch (error) {
      showError('Khng th cp nht thng tin. Vui lng th li.');
    } finally {
      setRefreshing(false);
    }
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    return new Date(dateString).toLocaleDateString('vi-VN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const TokenDisplay = ({ token, show, onToggle, label }) => {
    return (
      <div className="space-y-2">
        <div className="flex items-center justify-between">
          <span className="text-sm font-medium">{label}</span>
          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              onClick={onToggle}
            >
              {show ? <EyeSlashIcon className="h-4 w-4" /> : <EyeIcon className="h-4 w-4" />}
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => handleCopyToClipboard(token, label)}
              disabled={!token}
            >
              <CubeIcon className="h-4 w-4" />
            </Button>
          </div>
        </div>
        <div className="p-3 bg-muted rounded-md font-mono text-xs break-all max-h-24 overflow-y-auto">
          {show ? token : ''}
        </div>
      </div>
    );
  };

  if (!currentUser) {
    return (
      <div className="container py-8">
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary"></div>
        </div>
      </div>
    );
  }

  const decodedToken = firebaseToken ? decodeJwt(firebaseToken) : null;

  return (
    <div className="container py-6 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-4">
          <Avatar className="h-16 w-16">
            <AvatarImage src={userInfo?.avatar || currentUser.photoURL} />
            <AvatarFallback>
              {userInfo?.firstName?.[0] || currentUser.displayName?.[0] || currentUser.email?.[0]}
            </AvatarFallback>
          </Avatar>
          <div>
            <h1 className="text-3xl font-bold">
              {userInfo?.firstName && userInfo?.lastName 
                ? `${userInfo.firstName} ${userInfo.lastName}`
                : currentUser.displayName || 'Developer Profile'
              }
            </h1>
            <p className="text-muted-foreground flex items-center gap-2">
              {userInfo?.role === 'admin' && (
                <Badge variant="secondary" className="mr-2">
                  <ShieldCheckIcon className="h-3 w-3 mr-1" />
                  Admin
                </Badge>
              )}
              {userInfo?.email || currentUser.email}
            </p>
          </div>
        </div>
        <Button 
          variant="outline" 
          onClick={handleRefresh}
          disabled={refreshing}
        >
          <ArrowPathIcon className={`h-4 w-4 mr-2 ${refreshing ? 'animate-spin' : ''}`} />
          {refreshing ? 'ang cp nht...' : 'Cp nht'}
        </Button>
      </div>

      {/* Quick Token Access */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <KeyIcon className="h-5 w-5" />
            Backend API Token
          </CardTitle>
          <CardDescription>
            Token  truy cp API backend (s dng trong development)
          </CardDescription>
        </CardHeader>
        <CardContent>
          {backendToken ? (
            <TokenDisplay 
              token={backendToken}
              show={showBackendToken}
              onToggle={() => setShowBackendToken(!showBackendToken)}
              label="Backend Token"
            />
          ) : (
            <Alert>
              <AlertDescription>
                Khng tm thy backend token. Hy ng xut v ng nhp li.
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      {/* Main Content Tabs */}
      <Tabs defaultValue="profile" className="space-y-4">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="profile" className="flex items-center gap-2">
            <UserIcon className="h-4 w-4" />
            Thng tin
          </TabsTrigger>
          <TabsTrigger value="security" className="flex items-center gap-2">
            <ShieldCheckIcon className="h-4 w-4" />
            Bo mt
          </TabsTrigger>
          <TabsTrigger value="dev" className="flex items-center gap-2">
            <ComputerDesktopIcon className="h-4 w-4" />
            Dev Zone
          </TabsTrigger>
        </TabsList>

        {/* Profile Tab */}
        <TabsContent value="profile" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2">
            {/* Basic Information */}
            <Card>
              <CardHeader>
                <CardTitle>Thng tin c bn</CardTitle>
                <CardDescription>Thng tin c nhn t Firebase v Backend</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <UserIcon className="h-4 w-4" />
                    Tn hin th
                  </div>
                  <p className="font-medium">
                    {userInfo?.firstName && userInfo?.lastName 
                      ? `${userInfo.firstName} ${userInfo.lastName}`
                      : currentUser.displayName || 'Cha t tn'
                    }
                  </p>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <EnvelopeIcon className="h-4 w-4" />
                    Email
                  </div>
                  <p className="font-medium">{userInfo?.email || currentUser.email}</p>
                  <Badge variant={currentUser.emailVerified ? "default" : "destructive"} className="text-xs">
                    {currentUser.emailVerified ? " xc thc" : "Cha xc thc"}
                  </Badge>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <DevicePhoneMobileIcon className="h-4 w-4" />
                    S in thoi
                  </div>
                  <p className="font-medium">{userInfo?.phone || 'Cha cp nht'}</p>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <BuildingOfficeIcon className="h-4 w-4" />
                    T chc
                  </div>
                  <p className="font-medium">{userInfo?.organization || 'No organization'}</p>
                </div>

                {userInfo?.address && (
                  <>
                    <Separator />
                    <div className="space-y-2">
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                        <MapPinIcon className="h-4 w-4" />
                        a ch
                      </div>
                      <p className="font-medium">{userInfo.address}</p>
                    </div>
                  </>
                )}
              </CardContent>
            </Card>

            {/* Account Status */}
            <Card>
              <CardHeader>
                <CardTitle>Trng thi ti khon</CardTitle>
                <CardDescription>Thng tin ng nhp v hot ng</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <CalendarIcon className="h-4 w-4" />
                    Ln ng nhp cui
                  </div>
                  <p className="font-medium">{formatDate(userInfo?.lastLoginAt)}</p>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-sm text-muted-foreground">
                    <ComputerDesktopIcon className="h-4 w-4" />
                    Thit b ng nhp cui
                  </div>
                  <p className="text-sm">
                    {userInfo?.loginSessions?.[0]?.browser || 'N/A'} trn {userInfo?.loginSessions?.[0]?.os || 'N/A'}
                  </p>
                  <p className="text-xs text-muted-foreground">
                    IP: {userInfo?.lastLoginIp || 'N/A'}
                  </p>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="text-sm text-muted-foreground">Tng s ln ng nhp</div>
                  <p className="font-medium">{userInfo?.totalLogins || 0}</p>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="text-sm text-muted-foreground">Trng thi</div>
                  <Badge variant={userInfo?.isActive ? "default" : "destructive"}>
                    {userInfo?.isActive ? "Hot ng" : "Khng hot ng"}
                  </Badge>
                </div>

                <Separator />

                <div className="space-y-2">
                  <div className="text-sm text-muted-foreground">Ngy to ti khon</div>
                  <p className="font-medium">{formatDate(userInfo?.createdAt)}</p>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Full Backend Data */}
          {userInfo && (
            <Card>
              <CardHeader>
                <CardTitle>D liu Backend y </CardTitle>
                <CardDescription>Ton b thng tin ngi dng t backend API</CardDescription>
              </CardHeader>
              <CardContent>
                <CodeBlock data={userInfo} height={400} />
              </CardContent>
            </Card>
          )}
        </TabsContent>

        {/* Security Tab */}
        <TabsContent value="security" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Phng thc ng nhp</CardTitle>
                <CardDescription>Cc nh cung cp xc thc bn  lin kt</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {currentUser.providerData.map((provider) => (
                  <div key={provider.providerId} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <p className="font-medium">{provider.providerId.replace('.com', '')}</p>
                      <p className="text-sm text-muted-foreground">{provider.email}</p>
                    </div>
                    <Badge variant="outline"> lin kt</Badge>
                  </div>
                ))}
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Phin ng nhp</CardTitle>
                <CardDescription>Thng tin cc phin ng nhp gn y</CardDescription>
              </CardHeader>
              <CardContent>
                {userInfo?.loginSessions?.length > 0 ? (
                  <div className="space-y-3">
                    {userInfo.loginSessions.slice(0, 3).map((session, index) => (
                      <div key={index} className="p-3 border rounded-lg">
                        <div className="flex items-center justify-between mb-2">
                          <span className="font-medium">{session.browser}</span>
                          <Badge variant={session.isActive ? 'default' : 'secondary'} className="text-xs">
                            {session.isActive ? 'ang hot ng' : 'Khng hot ng'}
                          </Badge>
                        </div>
                        <div className="text-sm text-muted-foreground space-y-1">
                          <p className="flex items-center gap-2">
                            <ComputerDesktopIcon className="h-3 w-3" />
                            {session.os} - {session.device}
                          </p>
                          <p className="flex items-center gap-2">
                            <GlobeAltIcon className="h-3 w-3" />
                            IP: {session.ip}
                          </p>
                          <p className="flex items-center gap-2">
                            <CalendarIcon className="h-3 w-3" />
                            ng nhp: {formatDate(session.loginAt)}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p className="text-center text-muted-foreground py-4">Khng c thng tin phin ng nhp</p>
                )}
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        {/* Dev Zone Tab */}
        <TabsContent value="dev" className="space-y-6">
          <Alert>
            <ComputerDesktopIcon className="h-4 w-4" />
            <AlertDescription>
              <strong>Developer Zone:</strong> Khu vc ny cha thng tin nhy cm ch dnh cho developer. 
              Khng chia s cc token ny vi bt k ai.
            </AlertDescription>
          </Alert>

          <div className="grid gap-6 md:grid-cols-2">
            {/* Firebase Token */}
            <Card>
              <CardHeader>
                <CardTitle>Firebase ID Token</CardTitle>
                <CardDescription>Token xc thc Firebase  gi API</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <TokenDisplay 
                  token={firebaseToken}
                  show={showFirebaseToken}
                  onToggle={() => setShowFirebaseToken(!showFirebaseToken)}
                  label="Firebase Token"
                />
                
                {firebaseToken && decodedToken && (
                  <>
                    <Separator />
                    <div>
                      <h4 className="font-medium mb-2">Token Decoded:</h4>
                      <CodeBlock data={decodedToken} height={200} />
                    </div>
                  </>
                )}
              </CardContent>
            </Card>

            {/* Backend Token */}
            <Card>
              <CardHeader>
                <CardTitle>Backend API Token</CardTitle>
                <CardDescription>Token  truy cp trc tip backend API</CardDescription>
              </CardHeader>
              <CardContent>
                {backendToken ? (
                  <TokenDisplay 
                    token={backendToken}
                    show={showBackendToken}
                    onToggle={() => setShowBackendToken(!showBackendToken)}
                    label="Backend Token"
                  />
                ) : (
                  <Alert>
                    <AlertDescription>
                      Backend token khng kh dng. Hy ng xut v ng nhp li.
                    </AlertDescription>
                  </Alert>
                )}
              </CardContent>
            </Card>
          </div>

          {/* API Usage Examples */}
          <Card>
            <CardHeader>
              <CardTitle>Hng dn s dng API</CardTitle>
              <CardDescription>V d cch s dng token  gi API</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <h4 className="font-medium mb-2">1. Ly thng tin user hin ti:</h4>
                <CodeBlock 
                  data={{
                    method: "GET",
                    url: "https://wd-mpc-server-production.up.railway.app/api/users/me",
                    headers: {
                      "Authorization": `Bearer ${backendToken || 'YOUR_BACKEND_TOKEN'}`,
                      "Content-Type": "application/json"
                    }
                  }} 
                  height={120}
                />
              </div>
              
              <div>
                <h4 className="font-medium mb-2">2. Ly danh sch cuc thi:</h4>
                <CodeBlock 
                  data={{
                    method: "GET",
                    url: "https://wd-mpc-server-production.up.railway.app/api/contests",
                    headers: {
                      "Authorization": `Bearer ${backendToken || 'YOUR_BACKEND_TOKEN'}`,
                      "Content-Type": "application/json"
                    }
                  }} 
                  height={120}
                />
              </div>

              <div>
                <h4 className="font-medium mb-2">3. Curl command example:</h4>
                <div className="p-3 bg-muted rounded-md font-mono text-xs overflow-x-auto">
                  {`curl -X GET "https://wd-mpc-server-production.up.railway.app/api/users/me" \\
  -H "Authorization: Bearer ${backendToken || 'YOUR_BACKEND_TOKEN'}" \\
  -H "Content-Type: application/json"`}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* User Identifiers */}
          <Card>
            <CardHeader>
              <CardTitle>User Identifiers</CardTitle>
              <CardDescription>Cc ID nh danh ca user trong h thng</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">Firebase UID:</label>
                <div className="flex gap-2">
                  <code className="flex-1 p-2 bg-muted rounded text-xs">{currentUser.uid}</code>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleCopyToClipboard(currentUser.uid, 'Firebase UID')}
                  >
                    <CubeIcon className="h-4 w-4" />
                  </Button>
                </div>
              </div>

              {userInfo?._id && (
                <div className="space-y-2">
                  <label className="text-sm font-medium">Backend User ID:</label>
                  <div className="flex gap-2">
                    <code className="flex-1 p-2 bg-muted rounded text-xs">{userInfo._id}</code>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleCopyToClipboard(userInfo._id, 'Backend User ID')}
                    >
                      <CubeIcon className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Notification */}
      {notification.open && (
        <div className="fixed bottom-4 right-4 z-50">
          <Alert className={`${notification.severity === 'success' ? 'border-green-500' : 'border-red-500'}`}>
            <AlertDescription>{notification.message}</AlertDescription>
          </Alert>
        </div>
      )}
    </div>
  );
};

export default ProfilePage;